
import { CRUD, Item, Order, OrderStatus } from "@/interface";
import fetchPost from "@/mixins/fetchMixin";
import { formatTimeDate } from "@/mixins/formatDateMixin";
import { defineComponent } from "vue";

export default defineComponent({
  props: [
    "isShowOrderListModal",
    "orderList",
    "totalAmount",
    "systemData",
    "proxyData",
  ],
  emits: [
    "showOrderListModal",
    "removeOrderItem",
    "updateConfirmInfoToLocalStorage",
  ],
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    serviceCharge() {
      return this.systemData.serviceCharge10Percent
        ? this.totalAmount * 0.1
        : 0;
    },
    tax() {
      return this.systemData.tax6Percent ? this.totalAmount * 0.06 : 0;
    },
    roundingAdj() {
      const serviceCharge = parseFloat(this.serviceCharge.toFixed(2));
      const tax = parseFloat(this.tax.toFixed(2));
      return this.rounding(this.totalAmount + serviceCharge + tax);
    },
    nettTotal() {
      return (
        this.totalAmount + this.serviceCharge + this.tax + this.roundingAdj
      );
    },
  },
  methods: {
    showOrderListModal(value: boolean) {
      this.$emit("showOrderListModal", value);
    },
    removeOrderItem(id: number) {
      this.$emit("removeOrderItem", id);
      if (this.orderList.length === 0) {
        this.showOrderListModal(false);
      }
    },
    rounding(value: number) {
      const total = value.toFixed(2);
      const cen = parseInt(total.substring(total.length - 1, total.length));
      if (cen === 0) {
        return 0;
      } else if (cen <= 4) {
        return (cen / 100) * -1;
      } else {
        return (10 - cen) / 100;
      }
    },
    confirmOrder() {
      this.loading = true;
      const textItems = this.orderList.map(
        (item: Item) => `(x${item.added}) ${item.code} ${item.name}`
      );
      const order: Order = {
        id: 0,
        status: OrderStatus.waiting,
        tableNo: 0,
        items: textItems.join("\n"),
        amount: parseFloat(this.nettTotal.toFixed(2)),
        createdDate: formatTimeDate(new Date()),
        customerId: Date.now(),
      };

      fetchPost<Order>(
        CRUD.createWithInsert,
        "Orders",
        this.proxyData.fetchCode,
        order
      )
        .then((rep) => {
          this.loading = false;
          console.log(rep)
          this.$emit("updateConfirmInfoToLocalStorage", order.customerId);
        })
        .catch(() => {
          alert("Fail to submit, please inform waiters!");
        });
    },
  },
});
